	export default {
		data() {
			return {
				qian:null,
				wx:false,
				useUser:this.user,
				payData:null
			}
		},
		onLoad() {
		   
		},
		methods: {
			wxc:function(){
				// uni.getProvider({
				// 	service:'payment',
				// 	success:function(service,provider){
				// 		console.log(service,provider)
				// 	}
				// 	})
				
				let that=this;
				let usurl=that.url1+"/dinghai/user/loginwechat";
				if(that.qian!=null&&that.qian>0){
					uni.login({
					  provider: 'weixin',
					  success: function (loginRes) {
						console.log(loginRes)
					    console.log(loginRes.authResult);
					    // 获取用户信息
						uni.showLoading({
							title:"正在发起付款",
							mask:true
						})
						uni.request({
								url: usurl,
								header:{
									"Content-Type" : "application/x-www-form-urlencoded"
								},
								method:'GET',
								data: {
									uid:that.useUser.uid,
									js_code:loginRes.code
								},
						    success: (res) => {
								console.log(res)
								if(res.statusCode==200){
									let openid=res.data.data
									console.log('openid:'+ typeof openid)
									console.log(res)
									uni.request({
											url: that.url1+'/dinghai/Charge/insertAddress',
											header:{
												"Content-Type" : "application/x-www-form-urlencoded"
											},
											method:'POST',
											data: {
												uid:that.useUser.uid,
												chargeprice:that.qian
											},
									    success: (res) => {
											if(res.statusCode==200){
												let cj=res.data.flag;
												console.log(cj)
												uni.request({
														url:that.WXpay,
														header:{
															"Content-Type" : "application/x-www-form-urlencoded"
														},
														method:'GET',
														data: {
															openid:openid,
															money:cj.chargeprice,
															orderid:cj.orderid
														},
												    success: (res) => {
														console.log(res)
														if(res.statusCode==200){
															that.payData=res.data;
															uni.requestPayment({
															    provider: 'wxpay',
																// orderInfo:'',
															    timeStamp: that.payData.timeStamp,
															    nonceStr: that.payData.nonceStr,
															    package: that.payData.package,
															    signType: that.payData.signType,
															    paySign: that.payData.paySign,
															    success: function (res) {
																	uni.showLoading({
																		title: '付款成功',
																		mask: true
																	});
																	uni.request({
																	    url: that.url1+'/dinghai/user/updateMoney',
																		header:{
																			"Content-Type" : "application/x-www-form-urlencoded"
																		},
																		method:'POST',
																	    data: {
																			uid:that.useUser.uid,
																			money:cj.chargeprice
																		},
																	    success: (res) => {
																			if(res.statusCode==200){
																				uni.showToast({
																					title:'充值成功'
																				})
																				uni.navigateBack({
																					
																				})
																			}else{
																				uni.showToast({
																					title: '充值失败！请联系客服',
																					icon:"none"
																				});
																			}
																	    },
																		fail : function(err){
																			console.log(err)
																			uni.showToast({
																				title: '充值失败！',
																				icon:"none"
																			});
																		}
																	})
															    },
															    fail: function (err) {
																	uni.showToast({
																		title:'用户取消充值',
																		icon:'none'
																	})
															    }
															});
														}else{
															uni.showToast({
																title:"您的网络打了个盹！",
																icon:"none"
															})
														}
												    },
													fail : function(err){
														uni.showToast({
															title:"您的网络打了个盹",
															icon:"none"
														})
													}
												});
											}else{
												uni.showToast({
													title:"您的网络打了个盹！",
													icon:"none"
												})
											}
									    },
										fail : function(err){
											uni.showToast({
												title:"您的网络打了个盹",
												icon:"none"
											})
										}
									});
								}else{
									uni.showToast({
										title:"您的网络打了个盹！",
										icon:"none"
									})
								}
						    },
							fail : function(err){
								uni.showToast({
									title:"您的网络打了个盹",
									icon:"none"
								})
							}
						});
					    uni.getUserInfo({
					      provider: 'weixin',
					      success: function (infoRes) {
					        console.log('用户昵称为：' + infoRes.userInfo.nickName);
							console.log(infoRes)
					      }
					    });
					  }
					});
				}else{
					uni.showToast({
						title:'必须填写充值金额并且充值金额必须大于0元',
						icon:'none'
					})
				}
			},
			wxCJ:function(){
				this.wx=true
			},
			back : function(){
			   uni.navigateBack({
			  
			   });
			}
		 }
}
